//importerar de olika komponenterna hotelcard, navbar, pinkhero, footer som ska renderas p startsidan . Sedan importeras routes och route fr att gra applikationen mer interaktiv och tala om vart vra lnkar ska leda till. 
//importer bootstrap-icons bibliotek fr att kunna anvnda ikonerna fr support (telefon) och logga in (person)//
import 'bootstrap-icons/font/bootstrap-icons.css';
import './App.css';
import { useState } from 'react';
import { NavBar } from "./navbar";
import { Main } from "./main";
import {Footer} from "./footer";
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { ScrollToTop } from './scrollTo';
import BokaResa from "./bokaresa"; 
import Payment from "./payment";
import Confirmation from "./confirmation";
import ReadMore from "./readmore";



function App() {
  const [selectedValue, setSelectedValue] = useState({ departure: '', persons: '' });

  // Funktion fr att uppdatera valt vrde
  const handleSelectedValue = (details) => {
    setSelectedValue((prevDetails) => ({
      ...prevDetails,
      ...details
    }));
  };

  return (
    <Router basename="/react-hotel-app">
       <ScrollToTop />
    <div className="App">
      <NavBar />
      <Routes>
          <Route path="/" element={<Main/>}/>
          <Route path="/bokaresa/:hotelID" element={<BokaResa onSelect={handleSelectedValue} />} /> {/* Tar anvndaren till Boka-vyn med det specifika hotellets picandinfo*/}
            <Route path="/readmore/:hotelID" element={<ReadMore />} />
            {/* Tar anvndaren till Ls mer-vyn fr det valda hotellet*/}
          <Route path="/payment/:hotelID/" element={<Payment selectedValue={selectedValue} />}    /> {/* Tar anvndaren till Betalnings-vyn */}
          <Route path ="/confirmation" element={<Confirmation />}/> {/* Tar anvndaren till den sista vyn som r betalningsbekrftelsen */}
        </Routes>
        <Footer />
    </div>
     </Router>
  );
}

export default App;
