//useLocation r en hook som returnerar ett objekt med information om den aktuella URL:en som anvndaren befinner sig p och pathname r den egenskap som innehller den specifika vgen till den nuvarande vyn. Genom att anvnda pathname som beroendevariabel krs useEffect varje gng anvdaren byter till en ny vy. Null returneras fr att tala om fr React att inget behver renderas men att effekten nd ska kras.
import { useEffect } from 'react';
import { useLocation }from 'react-router-dom';

export function ScrollToTop () {
    const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0); 
    document.documentElement.style.scrollBehavior = 'auto';//terstller scrolleffekt.
  }, [pathname]);

  return null;
}

export default ScrollToTop;

