//Komponent som visar kalendern i bokaresa.jsx.
import React, { useState } from 'react'; 
import Calendar from 'react-calendar'; //importerar kalender frn react-biblioteket//
import 'react-calendar/dist/Calendar.css'; // Importera styling fr react kalendern


export function Calender() {
      const [date, setDate] = useState(new Date()); // Kod frn react-biblioteket.
    return (

    <div style={{
        display: 'flex',            // Stylar layouten fr diven som innehller kalendern.
        justifyContent: 'center',   
        alignItems: "center", 
        flexDirection: 'column', 
        margin: "20px",
        }}>
        <h2 style={{
            fontSize: "1.1rem",
            textTransform: "uppercase",
            paddingBottom: "20px",
            paddingTop: "30px"
            }}>
            Vlj datum
        </h2>
        <Calendar //Kod frn react-biblioteket
            onChange={setDate}
            value={date}
            style={{ width: "100%"}} 
        />
    </div>
)}